.mission-failed {
    width: 100%;
    background-image: linear-gradient(to right, rgba(255, 0, 0, 0), rgba(255, 255, 191, 1), rgba(255, 0, 0, 0));
    background-size: 100% 100%;
    font-size: 32px;
    color: #4682B4;
    text-shadow: 4px 4px 4px #000000;
    -webkit-text-stroke: 1px black;
    text-align: center;
    position: absolute;
    top: 200px;
    animation: fade 5s;
}

.mission-cleared {
    width: 100%;
    background-image: linear-gradient(to right, rgba(255, 0, 0, 0), rgba(255, 252, 127, 1), rgba(255, 0, 0, 0));
    font-size: 32px;
    color: #f5c71a;
    text-shadow: 4px 4px 4px #000000;
    text-align: center;
    position: absolute;
    top: 200px;
    animation: fade 7s;
}

.end-screen {
    width: 100vw;
    height: 100vh;
    position: absolute;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.end-screen > input {
    margin: 30px;
    border: 2px solid #0066b2;
    background-color: white;
    color: #6495ED;
}

input[type='button'] {
    border-radius: 5px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 20px;
    padding-right: 20px;
    font-size: 24px;
}

.end-screen > input[type='button']:hover {
    background-color: #6495ED;
    color: white;
}